import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_RESIZE_COL_WIDTH = Symbol('TABLE_RESIZE_COL_WIDTH');

export function resizeTableColWidth(payload: {
  node: Table;
  colIndex: number;
  delta: number;
  containerWidth?: number;
}) {
  const { node, colIndex, delta, containerWidth } = payload;
  return {
    type: TABLE_RESIZE_COL_WIDTH,
    payload: {
      node,
      colIndex,
      delta,
      containerWidth,
    },
  };
}

interface TableResizeTableColWidthPayload {
  node: Table;
  colIndex: number;
  delta: number;
  containerWidth?: number;
}

interface TableResizeTableColWidthAction {
  type: typeof TABLE_RESIZE_COL_WIDTH;
  payload: TableResizeTableColWidthPayload;
}

export function isTableResizeTableColWidthAction(
  action: Action,
): action is TableResizeTableColWidthAction {
  return action.type === TABLE_RESIZE_COL_WIDTH;
}
